import { Box, Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchChickenInfo } from '../../redux/infoAboutChicken';
import PasswordInput from '../Password/Password';

export default function ModalInfo() {
  const [open, setOpen] = useState(false);
  const [password, setPassword] = useState(['*', '*', '*', '*', '*', '*']);
  const [disabled, setDisabled] = useState(true);

  const { t } = useTranslation();

  const { buttonState } = useSelector((state) => state.buttonClick);

  const countBlockInterface = process.env.REACT_APP_COUNTER_BLOCK;

  const infoChick = useSelector((state) => state.infoChicken);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchChickenInfo());

    if (
      infoChick?.data?.count ===
      Number(countBlockInterface && buttonState === 'stop')
    ) {
      setOpen(true);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, infoChick.data.count]);

  const handleClose = () => {
    setOpen(false);
  };

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    borderRadius: '10px',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '75px',
      width: '800px',
      height: '220px',
    },
  }));

  useEffect(() => {
    if (
      password[0] === 1 &&
      password[1] === 1 &&
      password[2] === 1 &&
      password[3] === 1 &&
      password[4] === 1 &&
      password[5] === 1
    ) {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [password]);

  return (
    <>
      <div>
        <Dialog
          open={open}
          PaperProps={{
            sx: {
              width: { md: '70%', lg: '95%' },
              height: { md: '80%', lg: '100%' },
              maxWidth: { md: '800px', lg: '1700px' },
              background: '#bebebe',
            },
          }}
        >
          <DialogContent
            sx={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              flexDirection: 'column',
            }}
          >
            <Box sx={{ border: '3px solid red', padding: '50px' }}>
              <Typography
                sx={{
                  fontSize: { md: '45px', lg: '85px' },
                  color: 'var(--color)',
                  textAlign: 'center',
                }}
              >
                Необходимо очистить платформу
              </Typography>
              <Typography
                sx={{
                  fontSize: { md: '45px', lg: '85px' },
                  color: 'var(--color)',
                  textAlign: 'center',
                }}
              >
                и провести повторную калибровку весов <br /> в лабораторных
                условиях
              </Typography>
            </Box>
            <PasswordInput password={password} setPassword={setPassword} />
          </DialogContent>

          <DialogActions>
            <StyleButton
              onClick={handleClose}
              disabled={disabled}
              sx={{
                background: '#21eb06',
                ':disabled': {
                  background: '#d4d4d4',
                },
              }}
            >
              {t('Хорошо')}
            </StyleButton>
          </DialogActions>
        </Dialog>
      </div>
    </>
  );
}
