import { useState, useCallback, useEffect } from 'react';
import { makeRequest } from '../../helper/sendResponse';

export const useLastChickenFetch = () => {
  const [lastChickenHook, setLastChickenHook] = useState(0);

  const myTime = process.env.REACT_APP_TIME;

  const fetchLastChicken = useCallback(() => {
    makeRequest('GET', `http://${process.env.REACT_APP_API}/current_weight`)
      .then((response) => {
        setLastChickenHook(response);
      })
      .catch((error) => {
        console.error(error);
      });
  }, []);

  useEffect(() => {
    const intervalId = setInterval(fetchLastChicken, myTime);

    return () => clearInterval(intervalId);
  }, [fetchLastChicken, myTime]);

  return lastChickenHook?.weight;
};
