import React, { useEffect, useState } from 'react';
import { makeRequest } from '../../helper/sendResponse';

export const CurrentWeightFetch = () => {
  const myTime = process.env.REACT_APP_TIME;

  const [weightStart, setWeightStart] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(() => {
      makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/current_weight_live`
      )
        .then((response) => {
          setWeightStart(response);
        })
        .catch((error) => {
          console.error(error);
        });
    }, myTime);

    return () => clearInterval(intervalId);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <>{weightStart ? weightStart.weight.toFixed(0) : 0}</>;
};
