import './ContentSetParams.css';
import { Box, Button, Typography, styled } from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useTranslation } from 'react-i18next';
import ModalSetButton from './ModalSetButton';
import { useState } from 'react';
import weightPlatform from '../../../assets/image/weightPlatform.jpg';
import {
  CallReceived,
  NorthWest,
  CallMade,
  SouthEast,
} from '@mui/icons-material';
import { useSelector } from 'react-redux';

export const ContentSetParams = ({
  handleClick,
  open,
  handleClose,
  handlePost,
  weightRanges,
  handleClickPlus,
  handleClickMinus,
  setValueWeight,
  handleClickOpenSetButton,
  sendData,
}) => {
  const { t } = useTranslation();

  const pointRedux = useSelector((state) => state.postPoint);

  const [openSetButton, setOpenSetButton] = useState(false);

  const clickMinMaxButton = (key) => {
    setValueWeight(key);
    setOpenSetButton(true);
  };

  const StyleButton = styled(Button)(({ theme }) => ({
    marginRight: '20px',
    color: '#000000',
    fontFamily: 'var(--font-family_bold)',
    
    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '65px',
      width: '450px',
      height: '160px',
    },
  }));

  const StyleTypography = styled(Typography)(({ theme }) => ({
    fontFamily: 'var(--font-family_light)',
    textAlign: 'center',
    [theme.breakpoints.up('md')]: {
      fontSize: '45px',
      width: '170px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '75px',
      width: '275px',
    },
  }));

  const postData = () => {
    sendData();
    handlePost();
  };

  const handleWeightClick = (weight) => {
    handleClick(weight);
  };

  const handleMinMaxButtonClick = (key) => {
    if (key !== 'heavy') {
      clickMinMaxButton(key);
    }
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          sx: {
            width: '100%',
            height: { md: '95%', lg: '95%' },
            maxWidth: { md: '800px', lg: '1700px' },
            background: '#bebebe',
          },
        }}
      >
        <DialogTitle sx={{ textAlign: 'center' }}>
          <Typography
            sx={{
              fontSize: { md: '45px', lg: '75px' },
              fontFamily: 'var(--font-family_bold)',
            }}
          >
            {t('Параметры')} {t('сортировки')}
          </Typography>
        </DialogTitle>

        <DialogContent
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <Box
            sx={{
              position: 'relative',
              width: { md: '640px', lg: '1500px' },
              height: { md: '1430px', lg: '2180px' },
              display: 'flex',
              flexDirection: 'column',
              gap: '80px',
              justifyContent: 'flex-end',
              alignItems: 'center',
            }}
          >
            {Object.keys(pointRedux)
              .filter((el) => el !== 'loading')
              .map((weight) => (
                <Box
                  sx={{
                    display: 'flex',
                    gap: { md: '20px', lg: '120px' },
                    alignItems: 'center',
                  }}
                  key={weight}
                >
                  <button
                    className="btn_weight"
                    onClick={() => handleWeightClick(weight)}
                  >
                    {pointRedux[weight] === 1 && (
                      <CallReceived sx={{ fontSize: { md: 55, lg: 100 } }} />
                    )}
                    {pointRedux[weight] === 2 && (
                      <NorthWest sx={{ fontSize: { md: 55, lg: 100 } }} />
                    )}
                    {pointRedux[weight] === 3 && (
                      <CallMade sx={{ fontSize: { md: 55, lg: 100 } }} />
                    )}
                    {pointRedux[weight] === 4 && (
                      <SouthEast sx={{ fontSize: { md: 55, lg: 100 } }} />
                    )}
                  </button>

                  {weight === 'super_light' && (
                    <StyleTypography>{t('Легкая-')}</StyleTypography>
                  )}
                  {weight === 'light' && (
                    <StyleTypography>{t('Легкая')}</StyleTypography>
                  )}
                  {weight === 'medium' && (
                    <StyleTypography>{t('Средняя')}</StyleTypography>
                  )}
                  {weight === 'heavy' && (
                    <StyleTypography>{t('Тяжелая')}</StyleTypography>
                  )}
                  <button
                    className="btn_change_weight"
                    onClick={() => handleMinMaxButtonClick(weight)}
                  >
                    {weightRanges[weight].min} - {weightRanges[weight].max}
                  </button>
                </Box>
              ))}

            <Box>
              <img
                src={weightPlatform}
                alt="weightPlatform"
                className="imagePoint"
              />
            </Box>
          </Box>
        </DialogContent>

        <DialogActions>
          <StyleButton
            onClick={handleClose}
            sx={{
              background: '#eb0606',
            }}
          >
            {t('Закрыть')}
          </StyleButton>

          <StyleButton
            onClick={postData}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Установить')}
          </StyleButton>
        </DialogActions>

        <ModalSetButton
          handleClickPlus={handleClickPlus}
          handleClickMinus={handleClickMinus}
          openSetButton={openSetButton}
          setOpenSetButton={setOpenSetButton}
          handleClickOpenSetButton={handleClickOpenSetButton}
        />
      </Dialog>
    </div>
  );
};
