import { Box, styled } from '@mui/material';
import React from 'react';

export const Points = ({
  isClickedOne,
  isClickedTwo,
  isClickedThree,
  isClickedFour,
  isClickedOneLoading,
  isClickedTwoLoading,
  isClickedThreeLoading,
  isClickedFourLoading,
  handleClickOne,
  handleClickTwo,
  handleClickThree,
  handleClickFour,
}) => {
  const StylesBox = styled(Box)(({ theme }) => ({
    borderRadius: '50%',
    position: 'absolute',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',

    [theme.breakpoints.up('md')]: {
      fontSize: '48px',
      width: '100px',
      height: '100px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '90px',
      width: '250px',
      height: '250px',
    },
  }));

  const getBoxStyle = (isClicked, isLoading) => {
    let background, color;
    if (isClicked) {
      background = '#00ff00';
      color = '#ff0000';
    } else if (isLoading) {
      background = '#ffff00';
      color = '#5eff00';
    } else {
      background = '#ff0000';
      color = '#5eff00';
    }
    return { background, color };
  };

  return (
    <Box
      sx={{
        border: '2px solid #1b0d0d',
        background: '#a1a1a1',
        borderRadius: '10px',
        position: 'relative',
        width: { md: '620px', lg: '1500px' },
        height: { md: '400px', lg: '1000px' },
      }}
    >
      <StylesBox
        sx={{
          bottom: 20,
          left: 20,
          ...getBoxStyle(isClickedOne, isClickedOneLoading),
        }}
        onClick={handleClickOne}
      >
        1
      </StylesBox>

      <StylesBox
        sx={{
          top: 20,
          left: 20,
          ...getBoxStyle(isClickedTwo, isClickedTwoLoading),
        }}
        onClick={handleClickTwo}
      >
        2
      </StylesBox>

      <StylesBox
        sx={{
          top: 20,
          right: 20,
          ...getBoxStyle(isClickedThree, isClickedThreeLoading),
        }}
        onClick={handleClickThree}
      >
        3
      </StylesBox>

      <StylesBox
        sx={{
          bottom: 20,
          right: 20,
          ...getBoxStyle(isClickedFour, isClickedFourLoading),
        }}
        onClick={handleClickFour}
      >
        4
      </StylesBox>
    </Box>
  );
};
