import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Preloader from '../Preloader/Preloader';
import { memo } from 'react';

export const UsbToast = memo(({ loading, saveFileAll, saveFileDay }) => {
  return (
    <div>
      <div className="loading_block">
        {(loading.all || loading.day) && <Preloader />}
      </div>

      <div className="loading_block">
        {!loading.all && loading.save === 1 && saveFileAll?.result && (
          <div>{toast.success('Файл был сохранен!')}</div>
        )}
      </div>
      <div className="loading_block">
        {!loading.day && loading.save === 2 && saveFileDay?.result && (
          <div>{toast.success('Файл был сохранен!')}</div>
        )}
      </div>

      <div className="loading_block">
        {!loading.all && loading.save === 1 && saveFileAll?.result === 0 && (
          <div>{toast.info('Нет данных!')}</div>
        )}
      </div>
      <div className="loading_block">
        {!loading.day && loading.save === 2 && saveFileDay?.result === 0 && (
          <div>{toast.info('Нет данных!')}</div>
        )}
      </div>

      <div className="loading_block">
        {!loading.all && loading.save === 1 && !saveFileAll && (
          <div>{toast.error('Нет данных!')}</div>
        )}
      </div>
      <div className="loading_block">
        {!loading.day && loading.save === 2 && !saveFileDay && (
          <div>{toast.error('Нет данных!')}</div>
        )}
      </div>
      <ToastContainer
        className="toast-position"
        position="top-center"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        rtl={false}
        draggable
        theme="dark"
        style={{ fontSize: "74px", zIndex: "1000" }}
      />
    </div>
  );
});
