import './Conveyor.css';
import { useEffect, useState } from 'react';
import { makeRequest } from '../../helper/sendResponse';
import { useDispatch, useSelector } from 'react-redux';
import { fetchChickenData } from '../../redux/totalSlice';
import graph from '../../assets/icons/graph.png';
import chicken from '../../assets/icons/chicken.png';
import weight from '../../assets/icons/weight.png';
import cv from '../../assets/icons/cv.png';
import uni from '../../assets/icons/uni.png';

export const Conveyor = () => {
  const [currentCounters, setCurrentCounters] = useState('');

  const dispatch = useDispatch();
  const { data } = useSelector((state) => state.totalChicken);

  const myTime = process.env.REACT_APP_TIME;

  useEffect(() => {
    const interval = setInterval(() => {
      makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/get_current_counters`
      )
        .then((response) => {
          if (Object.keys(response).length !== 0) {
            setCurrentCounters(response);
          }
        })
        .catch((error) => {
          console.error(error);
        });
    }, myTime);

    return () => clearInterval(interval);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(fetchChickenData());
    }, myTime);
    return () => clearInterval(interval);
  }, [dispatch, myTime]);

  const total = data || {};
  const super_light = currentCounters?.super_light || {};
  const light = currentCounters?.light || {};
  const medium = currentCounters?.medium || {};
  const heavy = currentCounters?.heavy || {};

  return (
    <table border="2" width="100%" height="100%">
      <thead>
        <tr>
          <th className="tableTh">
            <img src={graph} alt="" className="imgAbout" />
          </th>
          <th>
            <img src={chicken} alt="" className="imgAbout" />
          </th>
          <th>
            <img src={weight} alt="" className="imgAbout" />
          </th>
          <th>
            <img src={cv} alt="" className="imgAbout" />
          </th>
          <th>
            <img src={uni} alt="" className="imgAbout" />
          </th>
        </tr>
      </thead>
      <tbody>
        <tr className="tableTr">
          <td className="tableTdFirst">{super_light?.limits || 0}</td>
          <td className="tableTdFirst">
            {super_light?.count?.toFixed(0) || 0}
          </td>
          <td>{super_light?.mean_weight?.toFixed(0) || 0}</td>
          <td>{super_light?.cv?.toFixed(1) || 0}</td>
          <td>{super_light?.uni?.toFixed(1) || 0}</td>
        </tr>
        <tr className="tableTr">
          <td>{light?.limits || 0}</td>
          <td>{light?.count?.toFixed(0) || 0}</td>
          <td>{light?.mean_weight?.toFixed(0) || 0}</td>
          <td>{light?.cv?.toFixed(1) || 0}</td>
          <td>{light?.uni?.toFixed(1) || 0}</td>
        </tr>
        <tr className="tableTr">
          <td>{medium?.limits || 0}</td>
          <td>{medium?.count?.toFixed(0) || 0}</td>
          <td>{medium?.mean_weight?.toFixed(0) || 0}</td>
          <td>{medium?.cv?.toFixed(1) || 0}</td>
          <td>{medium?.uni?.toFixed(1) || 0}</td>
        </tr>
        <tr className="tableTr">
          <td>{heavy?.limits || 0}</td>
          <td>{heavy?.count?.toFixed(0) || 0}</td>
          <td>{heavy?.mean_weight?.toFixed(0) || 0}</td>
          <td>{heavy?.cv?.toFixed(1) || 0}</td>
          <td>{heavy?.uni?.toFixed(1) || 0}</td>
        </tr>
        <tr className="tableTr">
          <td>{total?.limits || 0}</td>
          <td>{total?.count?.toFixed(0) || 0}</td>
          <td>{total?.mean_weight?.toFixed(0) || 0}</td>
          <td>{total?.cv?.toFixed(1) || 0}</td>
          <td>{total?.uni?.toFixed(1) || 0}</td>
        </tr>
      </tbody>
    </table>
  );
};
