import { createSlice } from '@reduxjs/toolkit';

const paramSortSlice = createSlice({
  name: 'overweight',
  initialState: {
    data: [],
    loading: false,
    error: null,
    stateModalParamState: false,
  },
  reducers: {
    // контроль нажатия кнопки продолжить сортировку стр перевески
    controlStateModalParam: (state, action) => {
      state.stateModalParamState = action.payload;
    },
  },
});

export default paramSortSlice.reducer;
export const { controlStateModalParam } = paramSortSlice.actions;
