import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Navigation } from 'swiper/modules';

import instr1 from '../../assets/instruction_image/instr1.jpg';
import instr2 from '../../assets/instruction_image/instr2.jpg';
import instr4 from '../../assets/instruction_image/instr4.jpg';
import instr5 from '../../assets/instruction_image/instr5.jpg';
import instr6 from '../../assets/instruction_image/instr6.jpg';
import instr7 from '../../assets/instruction_image/instr7.jpg';
import instr8 from '../../assets/instruction_image/instr8.jpg';
import instr9 from '../../assets/instruction_image/instr9.jpg';
import instr10 from '../../assets/instruction_image/instr10.jpg';
import instr11 from '../../assets/instruction_image/instr11.jpg';
import instr12 from '../../assets/instruction_image/instr12.jpg';
import instr13 from '../../assets/instruction_image/instr13.jpg';
import instr14 from '../../assets/instruction_image/instr14.jpg';
import instr15 from '../../assets/instruction_image/instr15.jpg';
import instr16 from '../../assets/instruction_image/instr16.jpg';
import instr17 from '../../assets/instruction_image/instr17.jpg';
import instr18 from '../../assets/instruction_image/instr18.jpg';
import instr19 from '../../assets/instruction_image/instr19.jpg';
import instr20 from '../../assets/instruction_image/instr20.jpg';
import instr21 from '../../assets/instruction_image/instr21.jpg';

import { LinkInstr } from './LinkInstr';

export const Instruction = ({ swiper, setSwiper }) => {
  const goToPage = (page) => swiper.slideTo(page);

  const instructionImages = [
    instr1,
    instr2,
    <LinkInstr goToPage={goToPage} />,
    instr4,
    instr5,
    instr6,
    instr7,
    instr8,
    instr9,
    instr10,
    instr11,
    instr12,
    instr13,
    instr14,
    instr15,
    instr16,
    instr17,
    instr18,
    instr19,
    instr20,
    instr21,
  ];

  const swiperSlides = instructionImages.map((image, index) => (
    <SwiperSlide key={index}>
      {typeof image === 'string' ? (
        <img src={image} alt="coding" className="instr_img" />
      ) : (
        image
      )}
    </SwiperSlide>
  ));

  return (
    <div className="report_block">
      <Swiper
        navigation={true}
        modules={[Navigation]}
        onSwiper={setSwiper}
        className="mySwiper"
      >
        {swiperSlides}
      </Swiper>
    </div>
  );
};
