import { Box } from '@mui/material';
import styles from './Chickens.module.css';
import { UseChicken } from './UseChicken';
import { overweightFetch } from '../../redux/overweightSlice';
import { useDispatch, useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';

export const Chickens = () => {
  const { state } = UseChicken();

  const { t } = useTranslation();

  const dispatch = useDispatch();

  const { overweightFetchControl } = useSelector(
    (state) => state.overweightChicken
  );

  const handleClickForRedux = (gender) => {
    dispatch(overweightFetch(gender));
  };

  const renderTable = (type) => {
    return (
      <table border="2" width="100%" height="100%">
        <thead>
          <tr className={styles.head}>
            <th></th>
            <th>{t('Легкая-')}</th>
            <th>{t('Легкая')}</th>
            <th>{t('Средняя')}</th>
            <th>{t('Тяжелая')}</th>
          </tr>
        </thead>
        <tbody>
          <tr className={styles.body}>
            <td>{t('Вес')}</td>
            <td>{state[type]?.super_light.weight?.toFixed(0) || 0}</td>
            <td>{state[type]?.light.weight?.toFixed(0) || 0}</td>
            <td>{state[type]?.medium.weight?.toFixed(0) || 0}</td>
            <td>{state[type]?.heavy.weight?.toFixed(0) || 0}</td>
          </tr>
          <tr className={styles.body}>
            <td>{t('Кол-во')}</td>
            <td>{state[type]?.super_light.quantity?.toFixed(0) || 0}</td>
            <td>{state[type]?.light.quantity?.toFixed(0) || 0}</td>
            <td>{state[type]?.medium.quantity?.toFixed(0) || 0}</td>
            <td>{state[type]?.heavy.quantity?.toFixed(0) || 0}</td>
          </tr>
        </tbody>
      </table>
    );
  };

  return (
    <Box
      sx={{
        marginTop: '40px',
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'center', gap: '20px' }}>
        <button
          className={
            overweightFetchControl === 'hen'
              ? styles.btn + ' ' + styles.active
              : styles.btn
          }
          onClick={() => handleClickForRedux('hen')}
        >
          {t('Курицы')}
        </button>
        <button
          className={
            overweightFetchControl === 'rooster'
              ? styles.btn + ' ' + styles.active
              : styles.btn
          }
          onClick={() => handleClickForRedux('rooster')}
        >
          {t('Петухи')}
        </button>
      </Box>

      <Box sx={{ height: '200px', fontSize: '45px', marginTop: '20px' }}>
        {overweightFetchControl === 'hen' && renderTable('chicken')}
        {overweightFetchControl === 'rooster' && renderTable('rooster')}
      </Box>
    </Box>
  );
};
