import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import styled from '@emotion/styled';
import SliderInstruction from '../SliderInstruction/SliderInstruction';
import { ConnectNetwork } from '../DownloadFiles/ConnectNetwork';
import { UsbNetwork } from '../DownloadFiles/UsbNetwork';

export default function ModalHeader({ openHeader, setOpenHeader, check }) {
  const { t } = useTranslation();

  const handleCloseHeaderModal = () => {
    setOpenHeader(false);
  };

  const StyleButton = styled(Button)({
    width: '240px',
    height: '100px',
    marginRight: '20px',
    color: '#000000',
    fontSize: '30px',
    fontFamily: 'var(--font-family_bold)',
  });

  return (
    <div>
      <Dialog
        open={openHeader}
        PaperProps={{
          sx: {
            width: '80%',
            height: '80%',
            maxWidth: { md: '800px', lg: '1200px' },
            background: '#bebebe',
          },
        }}
      >
        <DialogContent
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          {check === 1 && <UsbNetwork />}
          {check === 2 && (
            <SliderInstruction>
              <ConnectNetwork />
            </SliderInstruction>
          )}
        </DialogContent>

        <DialogActions>
          <StyleButton
            onClick={handleCloseHeaderModal}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Закрыть')}
          </StyleButton>
        </DialogActions>
      </Dialog>
    </div>
  );
}
