import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchData } from './api/fetchData';

export const fetchOverweightData = createAsyncThunk(
  'overweight/fetchOverweightData',
  async (gender) => {
    const response = await fetchData(
      `http://${process.env.REACT_APP_API}/get_weight_limits?gender=${gender}`
    );
    return response;
  }
);

const overweightSlice = createSlice({
  name: 'overweight',
  initialState: {
    data: [],
    loading: false,
    error: null,
    overweightFetchControl: 'rooster',
    controlSettingSortingState: 'beforeSorting',
  },
  reducers: {
    // изменения для таблицы стр перевески
    overweightFetch: (state, action) => {
      state.overweightFetchControl = action.payload;
    },
    // контроль нажатия кнопки продолжить сортировку стр перевески
    controlSettingSorting: (state, action) => {
      state.controlSettingSortingState = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchOverweightData.pending, (state) => {
      state.loading = true;
    });

    builder.addCase(fetchOverweightData.fulfilled, (state, action) => {
      state.loading = false;
      state.data = action.payload;
    });

    builder.addCase(fetchOverweightData.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message;
    });
  },
});

export default overweightSlice.reducer;
export const { overweightFetch, controlSettingSorting } =
  overweightSlice.actions;
