import React, { useState, useEffect } from 'react';

export const Timer = () => {
  const [seconds, setSeconds] = useState(5);

  useEffect(() => {
    if (seconds === 0) return;

    const countdown = setInterval(() => {
      setSeconds((prevSeconds) => prevSeconds - 1);
    }, 1000);

    return () => clearInterval(countdown);
  }, [seconds]);

  return <>{seconds} сек.</>;
};
