import { Box } from '@mui/material';
import { ButtonsControl } from '../Buttons/ButtonsControl/ButtonsControl';
import { LastChicken } from '../LastChicken/LastChicken';
import { TotalSumCurrent } from '../TotalSumCurrent/TotalSumCurrent';
import { Chickens } from './Chickens';
import styles from './Overweight.module.css';
import { controlSettingSorting } from '../../redux/overweightSlice';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { getButtonStop, pushButton } from '../../redux/buttonSlice';
import { controlStateModalParam } from '../../redux/paramSortSlice';

export const Overweight = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { t } = useTranslation();

  const handleControlContinueSorting = (pos) => {
    dispatch(controlSettingSorting(pos));
    dispatch(getButtonStop());
    dispatch(pushButton('stop'));
    navigate('/setting');
    // открытие модального окна параметров сортировки
    dispatch(controlStateModalParam(true));
  };

  return (
    <Box
      sx={{
        width: { md: '950px', lg: '1700px' },
        height: { md: '1070px', lg: '1700px' },
        border: '3px solid var(--border-sort-page)',
        borderRadius: '10px',
        mt: { md: '75px', lg: '0px' },
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <Box>
        <Box sx={{ marginTop: '-50px', marginBottom: '50px' }}>
          <LastChicken />
        </Box>

        <TotalSumCurrent />

        <Box
          sx={{
            width: { md: '835px', lg: '1295px' },
            height: '2px',
            background: 'var(--color)',
            marginTop: '40px',
          }}
        ></Box>

        <Box>
          <Chickens />
        </Box>

        <Box
          sx={{
            marginTop: { md: '200px', lg: '450px' },
            display: 'flex',
            flexDirection: 'row',
            gap: { md: '30px', lg: '50px' },
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <ButtonsControl />
          <Box>
            <button
              className={styles.button_control_over}
              onClick={() => handleControlContinueSorting('afterOverweight')}
            >
              {t('Сортировка')}
            </button>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
