import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const getButtonStart = createAsyncThunk('type/getStart', async () => {
  try {
    await axios.get(`http://${process.env.REACT_APP_API}/start`);
  } catch (err) {
    console.error(err);
  }
});

export const getButtonStop = createAsyncThunk('type/getStop', async () => {
  try {
    await axios.get(`http://${process.env.REACT_APP_API}/clear_measures`);
    // await axios.get('https://jsonplaceholder.typicode.com/users/1');
  } catch (err) {
    console.error(err);
  }
});

export const infoButton = createSlice({
  name: 'button',
  initialState: { buttonState: 0, report: '', whereReport: '' },
  reducers: {
    pushButton: (state, action) => {
      state.buttonState = action.payload;
    },
    pushReport: (state, action) => {
      const { report, whereReport } = action.payload;
      state.report = report;
      state.whereReport = whereReport;
    },
  },
});

export default infoButton.reducer;
export const { pushButton, pushReport } = infoButton.actions;
