import React, { useEffect, useState } from 'react';
import moment from 'moment';

const Clock = () => {
  const [time, setTime] = useState('');

  useEffect(() => {
    let TimeId = setInterval(() => setTime(moment().format('HH:mm')));
    return () => {
      clearInterval(TimeId);
    };
  }, [time]);

  return <>{time}</>;
};

export default Clock;
