import { LocalizationProvider } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import './Calendar.css';
import dayjs from 'dayjs';
import { Download } from '@mui/icons-material';
import 'dayjs/locale/ru';
import { StaticDatePicker } from '@mui/x-date-pickers/StaticDatePicker';

export function Calendar({ selectedDate, setSelectedDate }) {
  return (
    <>
      <button
        className="calendar_button"
        onClick={() =>
          setSelectedDate({
            ...selectedDate,
            openCalendar: true,
          })
        }
      >
        Выбрать дату <Download sx={{ fontSize: 45 }} />
      </button>

      <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
        <StaticDatePicker
          orientation="portrait"
          value={selectedDate}
          maxDate={dayjs().locale('ru')}
          onChange={(newValue) =>
            setSelectedDate({
              ...selectedDate,
              date: dayjs(newValue).format('YYYY-MM-DD'),
              openCalendar: false,
            })
          }
          slotProps={{
            actionBar: {
              actions: [],
            },
          }}
          className={selectedDate.openCalendar ? 'calendar' : 'calendar_closed'}
        />
      </LocalizationProvider>
    </>
  );
}
