import { Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';

export default function ModalFinish({
  open,
  handleClose,
  handleClickOpenReport,
}) {
  const { t } = useTranslation();

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    fontFamily: 'agro',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '55px',
      width: '400px',
      height: '120px',
    },
  }));

  return (
    <>
      <div>
        <Dialog
          open={open}
          onClose={handleClose}
          PaperProps={{
            sx: {
              width: '100%',
              height: '30%',
              maxWidth: { md: '880px', lg: '1400px' },
              background: '#bebebe',
            },
          }}
        >
          <DialogContent
            sx={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <Typography
              sx={{
                fontSize: { md: '40px', lg: '80px' },
                color: 'var(--color)',
                textAlign: 'center',
                fontFamily: 'var(--font-family_light)',
              }}
            >
              {t('Вы действительно хотите')}
              <br /> {t('завершить работу')}
              <br /> {t('и выгрузить отчет')} ?
            </Typography>
          </DialogContent>

          <DialogActions>
            <StyleButton
              onClick={handleClose}
              sx={{
                background: '#e01818',
              }}
            >
              {t('Отмена')}
            </StyleButton>
            <StyleButton
              onClick={() => handleClickOpenReport(1)}
              sx={{
                background: '#21eb06',
              }}
            >
              {t('Да')} - USB
            </StyleButton>
            <StyleButton
              onClick={() => handleClickOpenReport(2)}
              sx={{
                background: '#21eb06',
              }}
            >
              {t('Да')} - Wi-Fi
            </StyleButton>
          </DialogActions>
        </Dialog>
      </div>
    </>
  );
}
