import sun from '../../assets/icons/sun.png';
import moon from '../../assets/icons/moon.png';
import './DarkMode.css';
import { useState } from 'react';
import { useEffect } from 'react';

export const setThemeAndLocalStorage = (theme) => {
  document.querySelector('body').setAttribute('data-theme', theme);
  localStorage.setItem('selectedTheme', theme);
};
const DarkMode = () => {
  const [theme, setTheme] = useState(false);


  const toggleTheme = () => {
    const selectedTheme = localStorage.getItem('selectedTheme');
    if (selectedTheme === 'dark') {
      setThemeAndLocalStorage('light');
      setTheme(false);
    } else {
      setThemeAndLocalStorage('dark');
      setTheme(true);
    }
  };

  useEffect(() => {
    const selectedTheme = localStorage.getItem('selectedTheme');

    if (selectedTheme === 'dark') {
      setTheme(true);
      setThemeAndLocalStorage('dark');
    } else {
      setTheme(false);
      setThemeAndLocalStorage('light');
    }
  }, []);

  return (
    <div >
      {theme ? (
        <img src={moon} alt="moon" onClick={toggleTheme} className='iconTheme'/>
      ) : (
        <img src={sun} alt="sun" onClick={toggleTheme} className='iconTheme' />
      )}
    </div>
  );
};

export default DarkMode;
