import { Box } from '@mui/material';
import { Link, useLocation } from 'react-router-dom';
import home from '../../../assets/icons/home.png';
import filter from '../../../assets/icons/filter.png';
import weight_chicken from '../../../assets/icons/weight_chicken.png';
import services from '../../../assets/icons/services.png';
import './ButtonNav.css';
import usePageVisits from '../../../hooks/useVisitedPage';
import { makeRequest } from '../../../helper/sendResponse';
import { useDispatch, useSelector } from 'react-redux';
import { postData } from '../../../redux/postPoint';
// import { getButtonStart, pushButton } from '../../../redux/buttonSlice';

export const ButtonNav = () => {
  const [visitedPages] = usePageVisits({ setting: true });
  const location = useLocation();
  const dispatch = useDispatch();

  const { buttonState } = useSelector((state) => state.buttonClick);

  // перевеска
  const handlePostRedux = () => {
    const data = {
      super_light: 1,
      light: 1,
      medium: 1,
      heavy: 1,
    };
    makeRequest('GET', `http://${process.env.REACT_APP_API}/start_reweighting`);
    dispatch(postData(data));

    // кнопка старт
    // dispatch(getButtonStart());
    // dispatch(pushButton('start'));
  };

  // кнопка Блок
  const handleClickBlock = async () => {
    makeRequest('GET', `http://${process.env.REACT_APP_API}/calibrate_start`);
  };

  return (
    <Box
      sx={{
        paddingTop: '25px',
        display: 'flex',
        justifyContent: 'center',
        gap: { md: '70px', lg: '100px' },
        marginBottom: '30px',
        height: { md: '150px', lg: '300px' },
        alignItems: 'center',
      }}
    >
      <Link to={buttonState === 'stop' ? '/' : null}>
        <button
          className={location.pathname === '/' ? 'btn_nav_push' : 'btn_nav'}
        >
          <span>
            <img src={home} alt="home" className="icon_nav" />
          </span>
        </button>
      </Link>

      <button
        className={location.pathname === '/sort' ? 'btn_nav_push' : 'btn_nav'}
      >
        <span>
          <img src={filter} alt="spreadsheet" className="icon_nav" />
        </span>
      </button>

      <Link to={location.pathname === '/setting' ? '/overweight' : ''}>
        <button
          disabled={!visitedPages['/setting']}
          className={
            location.pathname === '/overweight' ? 'btn_nav_push' : 'btn_nav'
          }
          onClick={handlePostRedux}
        >
          <span>
            <img src={weight_chicken} alt="analytics" className="icon_nav" />
          </span>
        </button>
      </Link>

      <Link to={'/setting'}>
        <button
          className={
            location.pathname === '/setting' ? 'btn_nav_push' : 'btn_nav'
          }
          onClick={handleClickBlock}
        >
          <span>
            <img src={services} alt="setting" className="icon_nav" />
          </span>
        </button>
      </Link>
    </Box>
  );
};
