import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchData } from './api/fetchData';

export const fetchChickenData = createAsyncThunk(
  'chicken/fetchChickenData',
  async () => {
    const response = await fetchData(
      `http://${process.env.REACT_APP_API}/get_summarize_counter`
    );
    return response;
  }
);

const totalSlice = createSlice({
  name: 'chicken',
  initialState: {
    data: [],
    loading: false,
    error: null,
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchChickenData.pending, (state) => {
      state.loading = true;
    });

    builder.addCase(fetchChickenData.fulfilled, (state, action) => {
      state.loading = false;
      state.data = action.payload;
    });

    builder.addCase(fetchChickenData.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message;
    });
  },
});

export default totalSlice.reducer;
