import { useTranslation } from 'react-i18next';
import { makeRequest } from '../../../helper/sendResponse';
import '../Modal.css';
import { ContentSetParams } from './ContentSetParams';
import { useDispatch, useSelector } from 'react-redux';
import { increment, postData } from '../../../redux/postPoint';
import { UseStateParams } from './UseStateParams';
import { controlStateModalParam } from '../../../redux/paramSortSlice';

export default function ModalSetParams() {
  const { t } = useTranslation();

  const pointRedux = useSelector((state) => state.postPoint);
  const dispatch = useDispatch();

  const { stateModalParamState } = useSelector((state) => state.paramSortSlice);

  const { setValueWeight, weightRanges, handleClickPlus, handleClickMinus } =
    UseStateParams();

  const sendData = () => {
    makeRequest(
      'POST',
      `http://${process.env.REACT_APP_API}/set_data`,
      weightRanges
    );
  };

  const handleClickOpen = () => {
    dispatch(controlStateModalParam(true));
  };

  const handleClose = () => {
    dispatch(controlStateModalParam(false));
  };

  const handlePostRedux = () => {
    const data = {
      super_light: pointRedux.super_light,
      light: pointRedux.light,
      medium: pointRedux.medium,
      heavy: pointRedux.heavy,
    };
    dispatch(postData(data));
    dispatch(controlStateModalParam(false));
  };

  const handleClick = (value) => {
    dispatch(
      increment({
        ...pointRedux,
        [value]: pointRedux[value] === 4 ? 1 : pointRedux[value] + 1,
      })
    );
  };

  return (
    <div>
      <button className="button_settings" onClick={handleClickOpen}>
        {t('Параметры')}
        <br />
        {t('сортировки')}
      </button>
      <ContentSetParams
        handleClick={handleClick}
        open={stateModalParamState}
        weightRanges={weightRanges}
        handleClose={handleClose}
        handlePost={handlePostRedux}
        handleClickPlus={handleClickPlus}
        handleClickMinus={handleClickMinus}
        setValueWeight={setValueWeight}
        sendData={sendData}
      />
    </div>
  );
}
