import { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';

const usePageVisits = (initialPages) => {
  const [visitedPages, setVisitedPages] = useState(initialPages);
  const location = useLocation();

  useEffect(() => {
    const savedPages = JSON.parse(
      sessionStorage.getItem('visitedPages') || '{}'
    );
    setVisitedPages({ ...visitedPages, ...savedPages });

    if (!savedPages[location.pathname]) {
      savedPages[location.pathname] = true;
      setVisitedPages(savedPages);
      sessionStorage.setItem('visitedPages', JSON.stringify(savedPages));
    }

    const handleBeforeUnload = () => {
      sessionStorage.clear();
    };

    window.addEventListener('beforeunload', handleBeforeUnload);

    return () => {
      window.removeEventListener('beforeunload', handleBeforeUnload);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location]);

  return [visitedPages];
};

export default usePageVisits;
