import { Box } from '@mui/material';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { makeRequest } from '../../helper/sendResponse';
import './ButtonBalance.css';
import { useState } from 'react';
import ModalSetParams from '../Modal/SettingParameters/ModalSetParams';
import ModalWeight from '../Modal/PointsCalibration/ModalWeight';
import { useDispatch, useSelector } from 'react-redux';
import { getButtonStart, pushButton } from '../../redux/buttonSlice';
import { controlSettingSorting } from '../../redux/overweightSlice';
import { accuracyData } from '../../redux/accuracySlice';

export const ButtonBalance = () => {
  const { t } = useTranslation();

  const [loadTare, setLoadTare] = useState(false);

  const navigate = useNavigate();

  const { buttonState } = useSelector((state) => state.buttonClick);
  const dispatch = useDispatch();

  const stateAccuracy = useSelector((state) => state.accuracyPercentage);

  // Кнопка Старт
  const handleClickStart = async () => {
    dispatch(getButtonStart());
    dispatch(pushButton('start'));
    navigate('/sort');
    dispatch(controlSettingSorting('beforeSorting'));

    // отправка процентов из окна точности
    const data = {
      precision: `${stateAccuracy.roughSetupPercent}.${stateAccuracy.thinSetupPercent}`,
    };
    dispatch(accuracyData(data));
  };

  const handleClickContinue = () => {
    dispatch(getButtonStart());
    dispatch(pushButton('continue'));
    navigate('/sort');
  };

  // Тарировать весов
  const handleClickTare = async () => {
    setLoadTare(true);

    makeRequest('GET', `http://${process.env.REACT_APP_API}/tare`)
      .then(() => {
        setTimeout(() => {
          setLoadTare(false);
        }, 1000);
      })
      .catch((error) => {
        console.error(error);
        setLoadTare(false);
      });
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        border: '1px solid grey',
        width: { md: '800px', lg: '1700px' },
        height: { md: '490px', lg: '850px' },
        background: 'var(--card-background-color)',
        borderRadius: '10px',
      }}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexDirection: 'column',
          margin: 'auto',
          gap: '50px',
        }}
      >
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            gap: { md: '20px', lg: '100px' },
          }}
        >
          <button
            className={!loadTare ? 'button_settings' : 'button_settings load'}
            onClick={handleClickTare}
          >
            {t('Тарировать')}
          </button>

          <ModalWeight />
        </Box>

        <Box
          sx={{
            display: 'flex',
            flexDirection: 'row',
            gap: { md: '20px', lg: '100px' },
          }}
        >
          <ModalSetParams />
          {buttonState === 'stop' ? (
            <button className="button_settings" onClick={handleClickStart}>
              {t('Старт')}
            </button>
          ) : (
            <button className="button_settings" onClick={handleClickContinue}>
              {t('Продолжить')}
            </button>
          )}
        </Box>
      </Box>
    </Box>
  );
};
