import { Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import SliderInstruction from '../SliderInstruction/SliderInstruction';
import { UsbNetwork } from '../DownloadFiles/UsbNetwork';
import { ConnectNetwork } from '../DownloadFiles/ConnectNetwork';

export default function ModalReport({
  openReport,
  handleCloseReport,
  handleOpenFinish,
  changeSave,
}) {
  const { t } = useTranslation();

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    borderRadius: '5px',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '55px',
      width: '400px',
      height: '120px',
    },
  }));

  return (
    <>
      <div>
        <Dialog
          open={openReport}
          onClose={handleCloseReport}
          PaperProps={{
            sx: {
              width: '80%',
              height: changeSave === 1 ? '75%' : '85%',
              maxWidth: { md: '800px', lg: '1200px' },
              background: '#bebebe',
            },
          }}
        >
          <DialogContent
            sx={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              flexDirection: 'column',
            }}
          >
            <Typography
              sx={{
                fontSize: '40px',
                color: 'var(--color)',
                textAlign: 'center',
              }}
            ></Typography>

            {changeSave === 1 && <UsbNetwork />}

            {changeSave === 2 && (
              <SliderInstruction>
                <ConnectNetwork />
              </SliderInstruction>
            )}
          </DialogContent>

          <DialogActions>
            <StyleButton
              onClick={handleCloseReport}
              sx={{
                background: '#e01818',
              }}
            >
              {t('Отмена')}
            </StyleButton>
            <StyleButton
              onClick={handleOpenFinish}
              sx={{
                background: '#21eb06',
              }}
            >
              {t('Хорошо')}
            </StyleButton>
          </DialogActions>
        </Dialog>
      </div>
    </>
  );
}
