import { Typography } from '@mui/material';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import './LanguagesMode.css';

const LanguagesMode = () => {
  const { i18n } = useTranslation();
  const [changeLang, setChangeLang] = useState('en');

  const changeLanguage = (language) => {
    i18n.changeLanguage(language);
    setChangeLang(language);
  };

  const setLangAndLocalStorage = (changeLang) => {
    document.querySelector('body').setAttribute('data-lang', changeLang);
    localStorage.setItem('selectedLang', changeLang);
  };

  const toggleTheme = () => {
    const selectedLang = localStorage.getItem('selectedLang');
    if (selectedLang === 'eng') {
      setLangAndLocalStorage('ru');
      changeLanguage('ru');
    } else {
      setLangAndLocalStorage('eng');
      changeLanguage('en');
    }
  };

  useEffect(() => {
    const selectedLang = localStorage.getItem('selectedLang');

    if (selectedLang === 'eng') {
      setChangeLang('ru');

      setLangAndLocalStorage('eng');
    } else {
      setChangeLang('eng');
      setLangAndLocalStorage('ru');
    }
  }, []);

  return (
    <div className="lang_mode">
      {changeLang === 'en' ? (
        <Typography
          sx={{
            fontFamily: 'var(--font-family_bold)',
            fontSize: { md: '40px', lg: '60px' },
          }}
          onClick={toggleTheme}
        >
          EN
        </Typography>
      ) : (
        <Typography
          sx={{
            fontFamily: 'var(--font-family_bold)',
            fontSize: { md: '40px', lg: '60px' },
          }}
          onClick={toggleTheme}
        >
          РУ
        </Typography>
      )}
    </div>
  );
};

export default LanguagesMode;
