import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const postData = createAsyncThunk('type/postData', async (data) => {
  try {
    const response = await axios.post(
      `http://${process.env.REACT_APP_API}/set_drop_poses`,
      data
    );
    return response.data;
  } catch (err) {
    console.error(err);
  }
});

export const dataSlice = createSlice({
  name: 'point',
  initialState: { super_light: 1, light: 2, medium: 3, heavy: 4 },
  reducers: {
    increment: (state, action) => {
      state.super_light = action.payload.super_light;
      state.light = action.payload.light;
      state.medium = action.payload.medium;
      state.heavy = action.payload.heavy;
    },
  },
  extraReducers: (builder) => {
    // Do something while pending if you want.
    builder.addCase(postData.pending, (state) => {
      state.loading = true;
    });
    // Do something when passes.
    builder.addCase(postData.fulfilled, (state, action) => {
      state.loading = false;
      //   state.products = action.payload;
    });
    // Do something if fails.
    builder.addCase(postData.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message;
    });
  },
});

export default dataSlice.reducer;
export const { increment } = dataSlice.actions;
