import { Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import { Instruction } from '../SliderInstruction/Instruction';
import { useState } from 'react';

export default function ModalInstruction({
  openModalInstruction,
  setOpenModalInstruction,
}) {
  const { t } = useTranslation();

  const [swiper, setSwiper] = useState(null);

  const goToPage = (page) => swiper.slideTo(page);

  const handleCloseInstruction = () => {
    setOpenModalInstruction(false);
  };

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    borderRadius: '5px',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '55px',
      width: '450px',
      height: '120px',
    },
  }));

  return (
    <>
      <Dialog
        open={openModalInstruction}
        PaperProps={{
          sx: {
            width: { md: '80%', lg: '100%' },
            height: { md: '85%', lg: '95%' },
            maxWidth: { md: '800px', lg: '1700px' },
            background: '#bebebe',
          },
        }}
      >
        <DialogContent
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
          }}
        >
          <Typography
            sx={{
              fontSize: '40px',
              color: 'var(--color)',
              textAlign: 'center',
            }}
          ></Typography>

          <Instruction swiper={swiper} setSwiper={setSwiper} />
        </DialogContent>
        <DialogActions>
          <StyleButton
            onClick={() => goToPage(2)}
            sx={{
              background: '#e7eb06',
            }}
          >
            {t('Содержание')}
          </StyleButton>

          <StyleButton
            onClick={handleCloseInstruction}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Закрыть')}
          </StyleButton>
        </DialogActions>
      </Dialog>
    </>
  );
}
