import { DialogTitle, Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import SliderAccuracy from '../SliderAccuracy/SliderAccuracy';
import { accuracyData } from '../../redux/accuracySlice';
import { useDispatch, useSelector } from 'react-redux';

export default function ModalAccuracy({
  openModalAccuracy,
  setOpenModalAccuracy,
}) {
  const { t } = useTranslation();

  const dispatch = useDispatch();

  const stateAccuracy = useSelector((state) => state.accuracyPercentage);

  const handlePostAccuracy = () => {
    const data = {
      precision: `${stateAccuracy.roughSetupPercent}.${stateAccuracy.thinSetupPercent}`,
    };
    dispatch(accuracyData(data));
  };

  const handleCloseAccuracy = () => {
    setOpenModalAccuracy(false);
  };

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    borderRadius: '5px',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '75px',
      width: '600px',
      height: '170px',
    },
  }));

  return (
    <>
      <Dialog
        open={openModalAccuracy}
        PaperProps={{
          sx: {
            width: { md: '80%', lg: '100%' },
            height: { md: '85%', lg: '95%' },
            maxWidth: { md: '800px', lg: '1700px' },
            background: '#bebebe',
          },
        }}
      >
        <DialogTitle
          sx={{
            textAlign: 'center',
            fontSize: { md: '55px', lg: '85px' },
            color: 'var(--color)',
            fontFamily: 'var(--font-family_bold)',
          }}
        >
          {t('Режимы сортировки')}
        </DialogTitle>

        <DialogContent
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
          }}
        >
          <Typography
            sx={{
              fontSize: '40px',
              color: 'var(--color)',
              textAlign: 'center',
            }}
          ></Typography>

          <SliderAccuracy />
        </DialogContent>
        <DialogActions>
          <StyleButton
            onClick={handleCloseAccuracy}
            sx={{
              background: '#eb0606',
            }}
          >
            {t('Закрыть')}
          </StyleButton>

          <StyleButton
            onClick={handlePostAccuracy}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Установить')}
          </StyleButton>
        </DialogActions>
      </Dialog>
    </>
  );
}
