import { Box, Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { makeRequest } from '../../../helper/sendResponse';
import { WeightPoint } from './WeightPoint';
import { WeightSelection } from './WeightSelection';
import { Points } from './Points';
import '../Modal.css';

export default function ModalWeight() {
  const { t } = useTranslation();

  const [isClickedOne, setIsClickedOne] = useState(false);
  const [isClickedTwo, setIsClickedTwo] = useState(false);
  const [isClickedThree, setIsClickedThree] = useState(false);
  const [isClickedFour, setIsClickedFour] = useState(false);

  const [isClickedOneLoading, setIsClickedOneLoading] = useState(false);
  const [isClickedTwoLoading, setIsClickedTwoLoading] = useState(false);
  const [isClickedThreeLoading, setIsClickedThreeLoading] = useState(false);
  const [isClickedFourLoading, setIsClickedFourLoading] = useState(false);

  const [open, setOpen] = useState(false);

  const [changeWeight, setChangeWeight] = useState(500);

  const timeCalibrate = process.env.REACT_APP_TIME_CALIBRATE;

  const handleClickOpen = async () => {
    setOpen(true);
    try {
      await makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/calibrate_start`
      );
    } catch (error) {
      console.error(error);
    }
  };

  const handleClose = () => {
    setOpen(false);
  };

  // Калибровка весов
  const handleClickOne = async () => {
    setIsClickedOneLoading(true);
    try {
      await makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/calibrate_weight1`
      );
      await new Promise((resolve) => setTimeout(resolve, timeCalibrate));
      // await makeRequest('GET', 'https://jsonplaceholder.typicode.com/users');

      setIsClickedOne(true);
      setIsClickedOneLoading(false);
    } catch (error) {
      console.error(error);
    }
  };

  const handleClickTwo = async () => {
    try {
      if (isClickedOne) {
        setIsClickedTwoLoading(true);
        await makeRequest(
          'GET',
          `http://${process.env.REACT_APP_API}/calibrate_weight2`
        );
        await new Promise((resolve) => setTimeout(resolve, timeCalibrate));

        setIsClickedTwo(true);
        setIsClickedTwoLoading(false);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleClickThree = async () => {
    try {
      if (isClickedOne && isClickedTwo) {
        setIsClickedThreeLoading(true);
        await makeRequest(
          'GET',
          `http://${process.env.REACT_APP_API}/calibrate_weight3`
        );
        await new Promise((resolve) => setTimeout(resolve, timeCalibrate));

        setIsClickedThree(true);
        setIsClickedThreeLoading(false);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleClickFour = async () => {
    try {
      if (isClickedOne && isClickedTwo && isClickedThree) {
        setIsClickedFourLoading(true);
        await makeRequest(
          'GET',
          `http://${process.env.REACT_APP_API}/calibrate_weight4`
        );
        await new Promise((resolve) => setTimeout(resolve, timeCalibrate));

        setIsClickedFour(true);
        setIsClickedFourLoading(false);
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleClickFinish = async () => {
    try {
      setOpen(false);
      await makeRequest('POST', `http://${process.env.REACT_APP_API}/calibrate`, {
        weight: changeWeight,
      });
    } catch (error) {
      console.error(error);
    } finally {
      setOpen(false);
    }
  };

  const handleReset = () => {
    setIsClickedOne(false);
    setIsClickedTwo(false);
    setIsClickedThree(false);
    setIsClickedFour(false);
  };

  const StyleButton = styled(Button)(({ theme }) => ({
    width: '240px',
    height: '100px',
    marginRight: '20px',
    color: '#000000',
    fontSize: '30px',
    fontFamily: 'var(--font-family_bold)',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '70px',
      width: '500px',
      height: '200px',
    },
  }));

  return (
    <div>
      {window.location.pathname === '/setting' && (
        <button className="button_settings" onClick={handleClickOpen}>
          {t('Калибровка')}
        </button>
      )}
      <Dialog
        open={open}
        onClose={handleClose}
        PaperProps={{
          sx: {
            width: '100%',
            height: { md: '80%', lg: '95%' },
            maxWidth: { md: '800px', lg: '1700px' },
            background: '#bebebe',
          },
        }}
      >
        <DialogTitle sx={{ textAlign: 'center' }}>
          <Typography
            sx={{
              fontSize: { md: '45px', lg: '95px' },
              fontFamily: 'var(--font-family_bold)',
            }}
          >
            {t('Калибровка весов')}
          </Typography>
        </DialogTitle>

        <DialogContent
          sx={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            gap: '100px',
            flexDirection: 'column',
            overflow: 'auto',
          }}
        >
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              gap: '20px',
              alignItems: 'center',
            }}
          >
            <Typography
              sx={{
                fontSize: { md: '35px', lg: '75px' },
                fontFamily: 'var(--font-family_bold)',
              }}
            >
              {t('Выберите вес (грамм)')}
            </Typography>
          </Box>

          <Box
            sx={{
              display: 'flex',
              flexDirection: 'column',
              gap: '20px',
              marginTop: '-80px',
              alignItems: 'center',
            }}
          >
            <WeightSelection
              changeWeight={changeWeight}
              setChangeWeight={setChangeWeight}
            />
          </Box>

          <Points
            isClickedOne={isClickedOne}
            isClickedTwo={isClickedTwo}
            isClickedThree={isClickedThree}
            isClickedFour={isClickedFour}
            isClickedOneLoading={isClickedOneLoading}
            isClickedTwoLoading={isClickedTwoLoading}
            isClickedThreeLoading={isClickedThreeLoading}
            isClickedFourLoading={isClickedFourLoading}
            handleClickOne={handleClickOne}
            handleClickTwo={handleClickTwo}
            handleClickThree={handleClickThree}
            handleClickFour={handleClickFour}
          />
        </DialogContent>

        <DialogActions>
          <Box
            sx={{ width: '100%', display: 'flex', justifyContent: 'center' }}
          >
            <Box sx={{ height: '300px' }}>
              <WeightPoint
                isClickedOne={isClickedOne}
                isClickedTwo={isClickedTwo}
                isClickedThree={isClickedThree}
                isClickedFour={isClickedFour}
                isClickedOneLoading={isClickedOneLoading}
                isClickedTwoLoading={isClickedTwoLoading}
                isClickedThreeLoading={isClickedThreeLoading}
                isClickedFourLoading={isClickedFourLoading}
              />
            </Box>
          </Box>
        </DialogActions>

        <DialogActions>
          <StyleButton
            onClick={handleReset}
            sx={{
              background: '#e01818',
            }}
          >
            {t('Сброс')}
          </StyleButton>
          <StyleButton
            onClick={handleClickFinish}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Завершить')}
          </StyleButton>
        </DialogActions>
      </Dialog>
    </div>
  );
}
