import { Box, Typography, styled } from '@mui/material';
import { Timer } from '../../Timer/Timer';
import { useTranslation } from 'react-i18next';

const StyleTypography = styled(Typography)(({ theme }) => ({
  fontSize: { md: '40px', lg: '60px' },
  fontFamily: 'var(--font-family_bold)',
  [theme.breakpoints.up('md')]: {
    fontSize: '40px',
  },
  [theme.breakpoints.up('lg')]: {
    fontSize: '75px',
  },
}));

export const WeightPoint = ({
  isClickedOne,
  isClickedTwo,
  isClickedThree,
  isClickedFour,
  isClickedOneLoading,
  isClickedTwoLoading,
  isClickedThreeLoading,
  isClickedFourLoading,
}) => {
  const { t } = useTranslation();

  const renderText = (point, button) => (
    <StyleTypography>
      {t('Положите груз на точку')} {point} <br /> {t('и нажмите кнопку')}{' '}
      {button}
    </StyleTypography>
  );

  const renderLoading = () => (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <Typography sx={{ fontSize: { md: '40px', lg: '60px' } }}>
        {t('Ждите')}...
      </Typography>
      <Typography sx={{ fontSize: { md: '40px', lg: '60px' } }}>
        <Timer />
      </Typography>
    </Box>
  );

  return (
    <>
      <Box sx={{ width: '100%', display: 'flex', justifyContent: 'center' }}>
        <Box sx={{ height: '300px' }}>
          {!isClickedOne && !isClickedOneLoading && renderText(1, 1)}
          {isClickedOneLoading && renderLoading()}

          {isClickedOne &&
            !isClickedTwo &&
            !isClickedTwoLoading &&
            renderText(2, 2)}
          {isClickedTwoLoading && renderLoading()}

          {isClickedOne &&
            isClickedTwo &&
            !isClickedThree &&
            !isClickedThreeLoading &&
            renderText(3, 3)}
          {isClickedThreeLoading && renderLoading()}

          {isClickedOne &&
            isClickedTwo &&
            isClickedThree &&
            !isClickedFour &&
            !isClickedFourLoading &&
            renderText(4, 4)}
          {isClickedFourLoading && renderLoading()}

          {isClickedOne && isClickedTwo && isClickedThree && isClickedFour && (
            <StyleTypography>{t('Калибровка весов завершена')}</StyleTypography>
          )}
        </Box>
      </Box>
    </>
  );
};
