import { Box, Typography, styled } from '@mui/material';
import chicken from '../../assets/icons/chicken.png';
import weight from '../../assets/icons/weight.png';
import cv from '../../assets/icons/cv.png';
import uni from '../../assets/icons/uni.png';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchChickenData } from '../../redux/totalSlice';
import './TotalSumCurrent.css';

export const TotalSumCurrent = () => {
  const dispatch = useDispatch();
  const { data } = useSelector((state) => state.totalChicken);
  const myTime = process.env.REACT_APP_TIME;

  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(fetchChickenData());
    }, myTime);
    return () => clearInterval(interval);
  }, [dispatch, myTime]);

  const StyledBox = styled(Box)({
    width: '140px',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
    gap: '30px',
    flexDirection: 'column',
  });

  const StyledBoxLine = styled(Box)({
    width: '2px',
    height: '80px',
    background: 'var(--color)',
  });

  const StyledTypography = styled(Typography)({
    fontWeight: '300',
    color: 'var(--color)',
    fontFamily: 'var(--font-family_bold)',
  });

  const renderChickenData = (image, value) => {
    return (
      <StyledBox>
        <img src={image} alt="" className="imgAbout" />
        <StyledTypography sx={{ fontSize: { md: '45px', lg: '75px' } }}>
          {value ? value : 0}
        </StyledTypography>
      </StyledBox>
    );
  };

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        width: { md: '850px', lg: '1300px' },
      }}
    >
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
          gap: { md: '20px', lg: '50px' },
        }}
      >
        {renderChickenData(chicken, data.count)}
        <StyledBoxLine></StyledBoxLine>
        {renderChickenData(
          weight,
          data.mean_weight ? data.mean_weight.toFixed(0) : 0
        )}
        <StyledBoxLine></StyledBoxLine>
        {renderChickenData(cv, data.cv ? data.cv.toFixed(1) : 0)}
        <StyledBoxLine></StyledBoxLine>
        {renderChickenData(uni, data.uni ? data.uni.toFixed(1) : 0)}
      </Box>
    </Box>
  );
};
