import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchOverweightData } from '../../redux/overweightSlice';

export const UseChicken = () => {
  const dispatch = useDispatch();
  const { data } = useSelector((state) => state.overweightChicken);
  const { overweightFetchControl } = useSelector(
    (state) => state.overweightChicken
  );

  const timeTable = process.env.REACT_APP_TIME_TABLE_CHICKEN_ROOSTER;

  useEffect(() => {
    const interval = setInterval(() => {
      dispatch(fetchOverweightData(overweightFetchControl));
    }, 3000);
    return () => clearInterval(interval);
  }, [dispatch, overweightFetchControl, timeTable]);

  const initialState = {
    chicken: {
      super_light: { weight: 0, quantity: 0 },
      light: { weight: 0, quantity: 0 },
      medium: { weight: 0, quantity: 0 },
      heavy: { weight: 0, quantity: 0 },
    },
    rooster: {
      super_light: { weight: 0, quantity: 0 },
      light: { weight: 0, quantity: 0 },
      medium: { weight: 0, quantity: 0 },
      heavy: { weight: 0, quantity: 0 },
    },
  };

  const [state, setState] = useState(initialState);

  const [view, setView] = useState('rooster');

  const handleClick = (body) => {
    setView(body);
  };

  useEffect(() => {
    if (Object.keys(data).length !== 0) {
      setState((prevState) => ({
        ...prevState,
        chicken: {
          super_light: {
            weight: data?.super_light[0],
            quantity: data?.super_light[1],
          },
          light: {
            weight: data?.light[0],
            quantity: data?.light[1],
          },
          medium: {
            weight: data?.medium[0],
            quantity: data?.medium[1],
          },
          heavy: {
            weight: data?.heavy[0],
            quantity: data?.heavy[1],
          },
        },
        rooster: {
          super_light: {
            weight: data?.super_light[0],
            quantity: data?.super_light[1],
          },
          light: {
            weight: data?.light[0],
            quantity: data?.light[1],
          },
          medium: {
            weight: data?.medium[0],
            quantity: data?.medium[1],
          },
          heavy: {
            weight: data?.heavy[0],
            quantity: data?.heavy[1],
          },
        },
      }));
    }
  }, [view, data]);

  return { state, view, handleClick };
};
