import { Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { getButtonStop, pushButton } from '../../redux/buttonSlice';

export default function ModalTotalFinish({ openFinish, handleCloseFinish }) {
  const { t } = useTranslation();
  const navigate = useNavigate();

  const dispatch = useDispatch();

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    fontFamily: 'agro',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '55px',
      width: '400px',
      height: '120px',
    },
  }));

  const handleYesModalFinish = () => {
    dispatch(getButtonStop());
    dispatch(pushButton('stop'));
    navigate('/');
  };

  return (
    <>
      <div>
        <Dialog
          open={openFinish}
          onClose={handleCloseFinish}
          PaperProps={{
            sx: {
              width: '100%',
              height: '30%',
              maxWidth: { md: '880px', lg: '1400px' },
              background: '#bebebe',
            },
          }}
        >
          <DialogContent
            sx={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <Typography
              sx={{
                fontSize: { md: '40px', lg: '80px' },
                color: 'var(--color)',
                textAlign: 'center',
                fontFamily: 'var(--font-family_light)',
              }}
            >
              {t('Вы действительно хотите')}
              <br /> {t('завершить работу')} ?
            </Typography>
          </DialogContent>

          <DialogActions>
            <StyleButton
              onClick={handleCloseFinish}
              sx={{
                background: '#e01818',
              }}
            >
              {t('Отмена')}
            </StyleButton>
            <StyleButton
              onClick={handleYesModalFinish}
              sx={{
                background: '#21eb06',
              }}
            >
              {t('Да')}
            </StyleButton>
          </DialogActions>
        </Dialog>
      </div>
    </>
  );
}
