import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';

export const UseStateParams = () => {
  const [valueWeight, setValueWeight] = useState('');

  const { controlSettingSortingState } = useSelector(
    (state) => state.overweightChicken
  );

  // params overWeight //
  const { data } = useSelector((state) => state.overweightChicken);

  const [weightRanges, setWeightRanges] = useState(() => {
    if (controlSettingSortingState === 'beforeSorting') {
      const storedWeightRanges = window.localStorage.getItem('weightRanges');
      return storedWeightRanges
        ? JSON.parse(storedWeightRanges)
        : {
            super_light: {
              min: 0,
              max: 500,
            },
            light: {
              min: 501,
              max: 1500,
            },
            medium: {
              min: 1501,
              max: 2500,
            },
            heavy: {
              min: 2501,
              max: 5000,
            },
          };
    } else {
      return {
        super_light: {
          min: 0,
          max: 0,
        },
        light: {
          min: 0,
          max: 0,
        },
        medium: {
          min: 0,
          max: 0,
        },
        heavy: {
          min: 0,
          max: 0,
        },
      };
    }
  });

  useEffect(() => {
    if (controlSettingSortingState === 'beforeSorting') {
      window.localStorage.setItem('weightRanges', JSON.stringify(weightRanges));
    } else if (Object.keys(data).length !== 0) {
      setWeightRanges((prev) => {
        return {
          ...prev,
          super_light: {
            min: 0,
            max: data?.super_light[0].toFixed(0),
          },
          light: {
            min: data?.super_light[0].toFixed(0),
            max: data?.light[0].toFixed(0),
          },
          medium: {
            min: data?.light[0].toFixed(0),
            max: data?.medium[0].toFixed(0),
          },
          heavy: {
            min: data?.medium[0].toFixed(0),
            max: 5000,
          },
        };
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleClickPlus = (number) => {
    const updatedWeightRanges = { ...weightRanges };

    if (valueWeight === 'super_light') {
      if (
        updatedWeightRanges.super_light.max + number >
          updatedWeightRanges.light.max &&
        controlSettingSortingState === 'beforeSorting'
      )
        return;

      updatedWeightRanges.super_light.max =
        Number(updatedWeightRanges.super_light.max) + number;

      updatedWeightRanges.light.min =
        Number(updatedWeightRanges.light.min) + number;
    } else if (valueWeight === 'light') {
      if (
        updatedWeightRanges.light.max + number >
          updatedWeightRanges.medium.max &&
        controlSettingSortingState === 'beforeSorting'
      )
        return;

      updatedWeightRanges.light.max =
        Number(updatedWeightRanges.light.max) + number;
      updatedWeightRanges.medium.min =
        Number(updatedWeightRanges.medium.min) + number;
    } else if (valueWeight === 'medium') {
      if (
        updatedWeightRanges.medium.max + number >
          updatedWeightRanges.heavy.max &&
        controlSettingSortingState === 'beforeSorting'
      )
        return;

      updatedWeightRanges.medium.max =
        Number(updatedWeightRanges.medium.max) + number;
      updatedWeightRanges.heavy.min =
        Number(updatedWeightRanges.heavy.min) + number;
    }

    setWeightRanges(updatedWeightRanges);
  };

  const handleClickMinus = (number) => {
    const updatedWeightRanges = { ...weightRanges };

    if (valueWeight === 'super_light') {
      updatedWeightRanges.super_light.max = Math.max(
        0,
        updatedWeightRanges.super_light.max - number
      );
      updatedWeightRanges.light.min = Math.max(
        0,
        updatedWeightRanges.light.min - number
      );
    } else if (valueWeight === 'light') {
      if (
        updatedWeightRanges.light.max - number <=
        updatedWeightRanges.light.min
      )
        return null;
      updatedWeightRanges.light.max = Math.max(
        0,
        updatedWeightRanges.light.max - number
      );
      updatedWeightRanges.medium.min = Math.max(
        0,
        updatedWeightRanges.medium.min - number
      );
    } else if (valueWeight === 'medium') {
      if (
        updatedWeightRanges.medium.max - number <=
        updatedWeightRanges.medium.min
      )
        return null;
    }
    updatedWeightRanges.medium.max = Math.max(
      0,
      updatedWeightRanges.medium.max - number
    );
    updatedWeightRanges.heavy.min = Math.max(
      0,
      updatedWeightRanges.heavy.min - number
    );

    setWeightRanges(updatedWeightRanges);
  };

  return {
    setValueWeight,
    weightRanges,
    handleClickPlus,
    handleClickMinus,
  };
};
