import { Box, Typography } from '@mui/material';
import { useCallback, useEffect, useState } from 'react';
import { makeRequest } from '../../helper/sendResponse';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';

export const LastChicken = ({ flexDirection }) => {
  const [weightStart, setWeightStart] = useState(0);
  const [lastChicken, setLastChicken] = useState(0);

  const myTime = process.env.REACT_APP_TIME;

  const { t } = useTranslation();

  const location = useLocation();

  const fetchWeight = useCallback(() => {
    makeRequest('GET', `http://${process.env.REACT_APP_API}/current_weight`)
      .then((response) => {
        setWeightStart(response);
      })
      .catch((error) => {
        console.error(error);
      });
  }, []);

  const fetchLastChicken = useCallback(() => {
    makeRequest('GET', `http://${process.env.REACT_APP_API}/last_chicken`)
      .then((response) => {
        setLastChicken(response);
      })
      .catch((error) => {
        console.error(error);
      });
  }, []);

  useEffect(() => {
    const intervalId = setInterval(fetchWeight, myTime);

    return () => clearInterval(intervalId);
  }, [fetchWeight, myTime]);

  useEffect(() => {
    const intervalId = setInterval(fetchLastChicken, myTime);

    return () => clearInterval(intervalId);
  }, [fetchLastChicken, myTime]);

  const chickenWeight = lastChicken
    ? lastChicken.category === 3
      ? `${t('Тяжелая')}`
      : lastChicken.category === 2
      ? `${t('Средняя')}`
      : lastChicken.category === 1
      ? `${t('Легкая')}`
      : `${t('Легкая-')}`
    : `${t('Нет')}`;

  return (
    <>
      <Box sx={{ display: 'flex', flexDirection: 'column' }}>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection: flexDirection,
            gap: '50px',
          }}
        >
          <Typography
            sx={{
              fontSize: { md: '45px', lg: '85px' },
              fontFamily: 'var(--font-family_light)',
            }}
          >
            {t('Последний вес')}
          </Typography>
          <Typography
            sx={{
              fontSize: { md: '50px', lg: '85px' },
              fontFamily: 'var(--font-family_bold)',
            }}
          >
            {weightStart ? weightStart.weight.toFixed(0) : 0}
          </Typography>
        </Box>

        {location.pathname === '/sort' && (
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              flexDirection: flexDirection,
              gap: '50px',
            }}
          >
            <Typography
              sx={{
                fontSize: { md: '45px', lg: '85px' },
                fontFamily: 'var(--font-family_light)',
              }}
            >
              {t('Последняя птица')}
            </Typography>
            <Typography
              sx={{
                fontSize: { md: '50px', lg: '85px' },
                fontFamily: 'var(--font-family_bold)',
              }}
            >
              {chickenWeight}
            </Typography>
          </Box>
        )}
      </Box>
    </>
  );
};
