import { Box, Typography, styled } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useTranslation } from 'react-i18next';
import { useEffect } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import { fetchChickenInfo } from '../../redux/infoAboutChicken';

export default function ModalTotalInfo({ open, setOpen }) {
  const { t } = useTranslation();

  const infoChick = useSelector((state) => state.infoChicken);
  const dispatch = useDispatch();

  const timeInfo = process.env.REACT_APP_TIME_INFO;

  useEffect(() => {
    dispatch(fetchChickenInfo());

    const interval = setInterval(() => {
      dispatch(fetchChickenInfo());
    }, timeInfo);

    return () => {
      clearInterval(interval);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch]);

  const handleClose = () => {
    setOpen(false);
  };

  // общее количество
  const totalChicken = infoChick?.data?.count;
  // общий вес
  const totalWeight = infoChick?.data?.weight / 1000;
  // общее время
  const totalTime = infoChick?.data?.running_time;

  function formatTime(second) {
    const hours = Math.floor(second / 3600);
    const minutes = Math.floor((second % 3600) / 60);
    const remainingSeconds = second % 60;

    const formattedTime = `${hours} ч ${minutes} мин ${remainingSeconds} сек`;
    return formattedTime;
  }

  const StyleButton = styled(Button)(({ theme }) => ({
    color: '#000000',
    fontSize: '30px',
    margin: '0 auto',
    marginBottom: '30px',
    borderRadius: '5px',

    [theme.breakpoints.up('md')]: {
      width: '240px',
      height: '100px',
      fontSize: '30px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '55px',
      width: '400px',
      height: '120px',
    },
  }));

  const StyleTypography = styled(Typography)(({ theme }) => ({
    color: 'var(--color)',
    fontFamily: 'var(--font-family_light)',

    [theme.breakpoints.up('md')]: {
      fontSize: '40px',
    },
    [theme.breakpoints.up('lg')]: {
      fontSize: '75px',
    },
  }));

  const StyleBox = styled(Box)({
    display: 'flex',
    justifyContent: 'center',
    gap: '30px',
    alignItems: 'center',
    flexDirection: 'column',
    border: '1px solid #59db0d',
    padding: '20px',
  });

  return (
    <div>
      <Dialog
        open={open}
        PaperProps={{
          sx: {
            width: '80%',
            height: '70%',
            maxWidth: '800px',
            background: '#bebebe',
          },
        }}
      >
        <DialogTitle
          sx={{
            textAlign: 'center',
            fontSize: { md: '55px', lg: '75px' },
            color: 'var(--color)',
            fontFamily: 'var(--font-family_bold)',
          }}
        >
          {t('Общая информация')}
        </DialogTitle>

        <DialogContent>
          <Box
            sx={{
              display: 'flex',
              marginTop: '150px',
              gap: { md: '30px', lg: '120px' },
              flexDirection: 'column',
            }}
          >
            <StyleBox>
              <StyleTypography>{t('Всего птицы')}</StyleTypography>
              <Typography
                sx={{
                  fontWeight: 'bold',
                  fontFamily: 'var(--font-family_bold)',
                  fontSize: { md: '50px', lg: '65px' },
                }}
              >
                {totalChicken || 0} шт.
              </Typography>
            </StyleBox>

            <StyleBox>
              <StyleTypography>{t('Итого вес')}</StyleTypography>
              <Typography
                sx={{
                  fontWeight: 'bold',
                  fontFamily: 'var(--font-family_bold)',
                  fontSize: { md: '50px', lg: '65px' },
                }}
              >
                {totalWeight ? totalWeight.toFixed(1) : 0} кг.
              </Typography>
            </StyleBox>

            <StyleBox>
              <StyleTypography>{t('Итого время')}</StyleTypography>
              <Typography
                sx={{
                  fontWeight: 'bold',
                  fontFamily: 'var(--font-family_bold)',
                  fontSize: { md: '50px', lg: '65px' },
                }}
              >
                {totalTime ? formatTime(totalTime) : 0}
              </Typography>
            </StyleBox>
          </Box>
        </DialogContent>

        <DialogActions>
          <StyleButton
            onClick={handleClose}
            sx={{
              background: '#21eb06',
            }}
          >
            {t('Закрыть')}
          </StyleButton>
        </DialogActions>
      </Dialog>
    </div>
  );
}
