import { Box } from '@mui/material';
import './WeightSelection.css'

export const WeightSelection = ({ changeWeight, setChangeWeight }) => {
  const weights = [500, 1000, 1500, 2000, 3000];

  const handleChangeWeight = (value) => {
    setChangeWeight(value);
  };

  return (
    <Box sx={{ display: 'flex', gap: '20px' }}>
      {weights.map((weight) => (
        <button
          key={weight}
          onClick={(e) => handleChangeWeight(weight)}
          className={`btn_weight_section ${changeWeight === weight ? 'active' : ''}`}
        >
          {weight}
        </button>
      ))}
    </Box>
  );
};
