import './Password.css';

const PasswordInput = ({ password, setPassword }) => {
  const handleDigitClick = (digit) => {
    const index = password.findIndex((val) => val === '*');
    if (index !== -1) {
      setPassword((prev) => [
        ...prev.slice(0, index),
        digit,
        ...prev.slice(index + 1),
      ]);
    } else {
      setPassword((prev) => [
        prev[0],
        prev[1],
        prev[2],
        prev[3],
        prev[4],
        digit,
      ]);
    }
  };


  const handleDeleteClick = (a) => {
    if (typeof password[5] === 'number') {
      setPassword((prev) => [...prev.slice(0, 5), a]);
    } else if (typeof password[4] === 'number') {
      setPassword((prev) => [...prev.slice(0, 4), a, a]);
    } else if (typeof password[3] === 'number') {
      setPassword((prev) => [...prev.slice(0, 3), a, a, a]);
    } else if (typeof password[2] === 'number') {
      setPassword((prev) => [...prev.slice(0, 2), a, a, a, a]);
    } else if (typeof password[1] === 'number') {
      setPassword((prev) => [...prev.slice(0, 1), a, a, a, a, a]);
    } else {
      setPassword((prev) => [a, a, a, a, a, a]);
    }
  };

  return (
    <>
      <div className="password_text">Введите пароль</div>
      <div className="password_text stars">
        {password.map((item, index) => (
          <span key={index}>{item}</span>
        ))}
      </div>
      <div className="keyboard">
        <div className="row">
          {[1, 2, 3].map((item, index) => (
            <button
              key={index}
              onClick={() => handleDigitClick(item)}
              className="btn_keyboard"
            >
              {item}
            </button>
          ))}
        </div>

        <div className="row">
          {[4, 5, 6].map((item, index) => (
            <button
              key={index}
              onClick={() => handleDigitClick(item)}
              className="btn_keyboard"
            >
              {item}
            </button>
          ))}
        </div>

        <div className="row">
          {[7, 8, 9].map((item, index) => (
            <button
              key={index}
              onClick={() => handleDigitClick(item)}
              className="btn_keyboard"
            >
              {item}
            </button>
          ))}
        </div>

        <div className="row">
          <button
            onClick={() => handleDeleteClick('*')}
            className="btn_keyboard"
          >
            &lt;
          </button>
          <button onClick={() => handleDigitClick(0)} className="btn_keyboard">
            0
          </button>
          <button className="btn_keyboard">+</button>
        </div>
      </div>
    </>
  );
};

export default PasswordInput;
