import Button from '@mui/material/Button';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import logo_i_mono from '../../assets/icons/logo_i_mono.png';
import ModalTotalInfo from '../Modal/ModalTotalInfo';
import { useState } from 'react';
import {
  QueryStats,
  MenuBook,
  Usb,
  Wifi,
  CrisisAlert,
} from '@mui/icons-material';
import ModalInstruction from '../Modal/ModalInstruction';
import ModalHeader from '../Modal/ModalHeader';
import styles from './DropDown.module.css';
import { useTranslation } from 'react-i18next';
import ModalAccuracy from '../Modal/ModalAccuracy';

export default function DropDown() {
  const { t } = useTranslation();

  const [anchorEl, setAnchorEl] = useState(null);

  const [openModalInfo, setOpenModalInfo] = useState(false);
  const [openModalInstruction, setOpenModalInstruction] = useState(false);

  const [openModalAccuracy, setOpenModalAccuracy] = useState(false);

  const [openHeader, setOpenHeader] = useState(false);
  const [check, setCheck] = useState(0);

  const handleOpenHeaderModal = (number) => {
    setCheck(number);
    setOpenHeader(true);
  };

  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleOpenModalTotal = () => {
    setOpenModalInfo(true);
  };

  const handleOpenModalInstruction = () => {
    setOpenModalInstruction(true);
  };

  const handleOpenModalAccuracy = () => {
    setOpenModalAccuracy(true);
  };

  return (
    <div className={styles.dropdown}>
      <ModalTotalInfo open={openModalInfo} setOpen={setOpenModalInfo} />
      <ModalInstruction
        openModalInstruction={openModalInstruction}
        setOpenModalInstruction={setOpenModalInstruction}
      />

      <ModalAccuracy
        openModalAccuracy={openModalAccuracy}
        setOpenModalAccuracy={setOpenModalAccuracy}
      />

      <Button
        id="basic-button"
        aria-controls={open ? 'basic-menu' : undefined}
        aria-haspopup="true"
        aria-expanded={open ? 'true' : undefined}
        onClick={handleClick}
      >
        <img src={logo_i_mono} alt="logo" className={styles.logo_i} />
      </Button>

      <Menu
        id="basic-menu"
        anchorEl={anchorEl}
        open={open}
        onClose={handleClose}
        MenuListProps={{
          'aria-labelledby': 'basic-button',
        }}
        PaperProps={{
          sx: {
            width: { md: '650px', lg: '1000px' },
            height: { md: '360px', lg: '680px' },
          },
        }}
      >
        <MenuItem
          onClick={handleClose}
          sx={{ fontSize: { md: '50px', lg: '80px' } }}
        >
          <CrisisAlert
            sx={{ fontSize: { md: '50px', lg: '100px' }, marginRight: '20px' }}
          />
          <div onClick={handleOpenModalAccuracy}>{t('Режимы сортировки')}</div>
        </MenuItem>
        <MenuItem
          onClick={handleClose}
          sx={{ fontSize: { md: '50px', lg: '80px' } }}
        >
          <QueryStats
            sx={{ fontSize: { md: '50px', lg: '100px' }, marginRight: '20px' }}
          />
          <div onClick={handleOpenModalTotal}>{t('Статистика')}</div>
        </MenuItem>
        <MenuItem
          onClick={handleClose}
          sx={{ fontSize: { md: '50px', lg: '80px' } }}
        >
          <MenuBook
            sx={{ fontSize: { md: '50px', lg: '100px' }, marginRight: '20px' }}
          />
          <div onClick={handleOpenModalInstruction}>{t('Инструкция')}</div>
        </MenuItem>
        <MenuItem
          onClick={handleClose}
          sx={{ fontSize: { md: '50px', lg: '80px' } }}
        >
          <Usb
            sx={{ fontSize: { md: '50px', lg: '100px' }, marginRight: '20px' }}
          />
          <div onClick={() => handleOpenHeaderModal(1)}>
            {t('Выгрузить отчет USB')}
          </div>
        </MenuItem>
        <MenuItem
          onClick={handleClose}
          sx={{ fontSize: { md: '50px', lg: '80px' } }}
        >
          <Wifi
            sx={{ fontSize: { md: '50px', lg: '100px' }, marginRight: '20px' }}
          />
          <div onClick={() => handleOpenHeaderModal(2)}>
            {t('Выгрузить отчет Wi-Fi')}
          </div>
        </MenuItem>
      </Menu>
      <ModalHeader
        openHeader={openHeader}
        setOpenHeader={setOpenHeader}
        check={check}
      />
    </div>
  );
}
