import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { fetchData } from './api/fetchData';

export const fetchChickenInfo = createAsyncThunk(
  'chicken/fetchChickenInfo',
  async () => {
    const response = await fetchData(
      `http://${process.env.REACT_APP_API}/all_time_info`
      //   'http://jsonplaceholder.typicode.com/users/1'
    );
    return response;
  }
);

const infoAboutChicken = createSlice({
  name: 'info',
  initialState: {
    data: [],
    loading: false,
    error: null,
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(fetchChickenInfo.pending, (state) => {
      state.loading = true;
    });

    builder.addCase(fetchChickenInfo.fulfilled, (state, action) => {
      state.loading = false;
      state.data = action.payload;
    });

    builder.addCase(fetchChickenInfo.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message;
    });
  },
});

export default infoAboutChicken.reducer;
