import { Box } from '@mui/material';
import { useState } from 'react';
import './ButtonControl.css';
import { useTranslation } from 'react-i18next';
import ModalFinish from '../../Modal/ModalFinish';
import ModalReport from '../../Modal/ModalReport';
import ModalTotalFinish from '../../Modal/ModalTotalFinish';
import { useLocation } from 'react-router-dom';

export const ButtonsControl = () => {
  const { t } = useTranslation();
  const location = useLocation();

  const [open, setOpen] = useState(false);
  const [openReport, setOpenReport] = useState(false);
  const [openFinish, setOpenFinish] = useState(false);

  const [changeSave, setChangeSave] = useState('');

  const handleClickOpen = async () => {
    try {
      setOpen(true);
    } catch (error) {
      console.error(error);
    }
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleClickOpenReport = (number) => {
    setOpenReport(true);
    handleClose(false);
    setChangeSave(number);
  };

  const handleCloseReport = () => {
    setOpenReport(false);
  };

  const handleOpenFinish = () => {
    setOpenFinish(true);
    setOpenReport(false);
  };

  const handleCloseFinish = () => {
    setOpenFinish(false);
  };

  return (
    <>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
        }}
      >
        <button
          className={
            location.pathname === '/sort'
              ? 'button_control'
              : 'button_control_over'
          }
          onClick={handleClickOpen}
        >
          {t('Завершить')}
        </button>
      </Box>

      <ModalFinish
        open={open}
        handleClose={handleClose}
        handleClickOpenReport={handleClickOpenReport}
      />

      <ModalReport
        openReport={openReport}
        setOpenReport={setOpenReport}
        handleCloseReport={handleCloseReport}
        handleOpenFinish={handleOpenFinish}
        changeSave={changeSave}
      />

      <ModalTotalFinish
        openFinish={openFinish}
        handleCloseFinish={handleCloseFinish}
      />
    </>
  );
};
