import { configureStore } from '@reduxjs/toolkit';
import totalSliceReducer from './totalSlice';
import infoAboutChicken from './infoAboutChicken';
import postPointReducer from './postPoint';
import infoButtonReducer from './buttonSlice';
import overweightSliceReducer from './overweightSlice';
import accuracyReducer from './accuracySlice';
import paramSortSlice from './paramSortSlice';

import storage from 'redux-persist/lib/storage';
import { persistReducer } from 'redux-persist';
import { combineReducers } from '@reduxjs/toolkit';
import {
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';

const persistConfig = {
  key: 'root',
  blacklist: ['infoChicken', 'totalChicken'],
  storage,
};

const rootReducer = combineReducers({
  totalChicken: totalSliceReducer,
  infoChicken: infoAboutChicken,
  postPoint: postPointReducer,
  buttonClick: infoButtonReducer,
  overweightChicken: overweightSliceReducer,
  accuracyPercentage: accuracyReducer,
  // модальное окно параметры сортировки
  paramSortSlice: paramSortSlice,
});

const persistedReducer = persistReducer(persistConfig, rootReducer);

export const store = configureStore({
  reducer: persistedReducer,

  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }),
});
