import { Box, Typography } from '@mui/material';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import { useTranslation } from 'react-i18next';
import './ModalSetButton.css';

export default function ModalSetButton({
  handleClickPlus,
  handleClickMinus,
  openSetButton,
  setOpenSetButton,
}) {
  const { t } = useTranslation();

  const handleClose = () => {
    setOpenSetButton(false);
  };

  return (
    <Dialog
      open={openSetButton}
      onClose={handleClose}
      PaperProps={{
        sx: {
          width: '100%',
          maxWidth: { md: '800px', lg: '1700px' },
          height: { md: '385px', lg: '800px' },
          background: '#bebebe',
          mb: '-890px',
        },
      }}
    >
      <DialogTitle sx={{ textAlign: 'center' }}>
        <Typography
          sx={{
            fontSize: { md: '45px', lg: '80px' },
            fontFamily: 'var(--font-family_bold)',
          }}
        >
          {t('Установите верхний предел веса')}
        </Typography>
      </DialogTitle>

      <DialogContent
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          gap: '100px',
          flexDirection: 'column',
          overflow: 'auto',
        }}
      >
        <Box sx={{ display: 'flex', gap: '10px' }}>
          <button
            className="btn_weight_sort plus"
            onClick={() => handleClickPlus(1)}
          >
            +1
          </button>
          <button
            className="btn_weight_sort plus"
            onClick={() => handleClickPlus(10)}
          >
            +10
          </button>
          <button
            className="btn_weight_sort plus"
            onClick={() => handleClickPlus(100)}
          >
            +100
          </button>
          <button
            className="btn_weight_sort minus"
            onClick={() => handleClickMinus(1)}
          >
            -1
          </button>
          <button
            className="btn_weight_sort minus"
            onClick={() => handleClickMinus(10)}
          >
            -10
          </button>
          <button
            className="btn_weight_sort minus"
            onClick={() => handleClickMinus(100)}
          >
            -100
          </button>
        </Box>
      </DialogContent>

      <DialogActions>
        <Button
          onClick={handleClose}
          sx={{
            background: '#21eb06',
            width: { md: '240px', lg: '450px' },
            height: { md: '100px', lg: '150px' },
            marginRight: '20px',
            color: '#000000',
            fontSize: { md: '30px', lg: '65px' },
            fontFamily: 'var(--font-family_bold)',
          }}
        >
          {t('Ok')}
        </Button>
      </DialogActions>
    </Dialog>
  );
}
