import { Typography } from '@mui/material';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import { useDispatch, useSelector } from 'react-redux';
import {
  changeRoughSetupPercent,
  changeThinSetupPercent,
} from '../../redux/accuracySlice';

export default function SliderAccuracy() {
  const dispatch = useDispatch();
  const stateAccuracy = useSelector((state) => state.accuracyPercentage);

  const handleReduxRough = (_, value) => {
    dispatch(changeRoughSetupPercent(value));
  };

  const handleReduxThin = (_, value) => {
    dispatch(changeThinSetupPercent(value));
  };

  const sliderStyle = {
    width: { md: 100, lg: 200 },
    color: 'var(--card-background-color)',
    '& .MuiSlider-thumb': {
      height: { md: 50, lg: 100 },
      width: { md: 50, lg: 100 },
      color: 'white',
    },
    '& .MuiSlider-mark': {
      backgroundColor: '#3ec2498c',
      height: 10,
      width: { md: 120, lg: 250 },
      '&.MuiSlider-markActive': {
        opacity: 0.7,
        backgroundColor: 'currentColor',
      },
    },
  };

  const boxStyle = {
    width: { md: 150, lg: 1300 },
    height: { md: 800, lg: 1800 },
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    gap: '30px',
    flexDirection: 'column',
  };

  const typographyStyle = {
    fontSize: { md: '50px', lg: '75px' },
    fontFamily: 'var(--font-family_bold)',
    textAlign: 'center',
  };

  const sliderProps = {
    orientation: 'vertical',
    ariaLabel: 'accuracy',
    step: 1,
    marks: true,
    sx: sliderStyle,
  };

  return (
    <Box sx={boxStyle} style={{ flexDirection: 'row' }}>
      <Box sx={boxStyle}>
        <Slider
          {...sliderProps}
          value={stateAccuracy?.roughSetupPercent}
          min={1}
          max={10}
          onChange={handleReduxRough}
        />
        <Typography sx={typographyStyle}>Грубая настойка</Typography>
      </Box>
      <Box sx={{ display: 'flex', gap: '30px', flexDirection: 'row' }}>
        <Typography
          sx={{
            width: { md: '245px', lg: '575px' },
            fontSize: { md: '75px', lg: '150px' },
            fontFamily: 'var(--font-family_bold)',
            display: 'flex',
            justifyContent: 'center',
            textAlign: 'center',
          }}
        >
          &plusmn; {stateAccuracy?.roughSetupPercent},
          {stateAccuracy?.thinSetupPercent} %
        </Typography>
      </Box>
      <Box sx={boxStyle}>
        <Slider
          {...sliderProps}
          value={stateAccuracy?.thinSetupPercent}
          min={0}
          max={9}
          onChange={handleReduxThin}
        />
        <Typography sx={typographyStyle}>Тонкая настойка</Typography>
      </Box>
    </Box>
  );
}
