import { makeRequest } from '../../helper/sendResponse';
import '../SliderInstruction/Slider.css';
import { Download } from '@mui/icons-material';
import usb_port from '../../assets/image/usb_port.jpg';
import { useEffect, useState } from 'react';
import { UsbToast } from './UsbToast';
import { Calendar } from '../Calendar/Calendar';
import dayjs from 'dayjs';
import { useTranslation } from 'react-i18next';

export const UsbNetwork = () => {
  const { t } = useTranslation();

  const timeUsb = process.env.REACT_APP_TIME_USB;

  // eslint-disable-next-line no-unused-vars
  const [saveFileAll, setSaveFileAll] = useState('');
  const [saveFileDay, setSaveFileDay] = useState('');
  const [loading, setLoading] = useState({
    day: false,
    all: false,
    save: 0,
  });

  const [mountFlash, setMountFlash] = useState({ output: 0 });

  const currentDate = new Date();
  const formattedDate = currentDate.toISOString().split('T')[0];

  const [selectedDate, setSelectedDate] = useState({
    date: formattedDate,
    openCalendar: false,
  });

  useEffect(() => {
    if (mountFlash?.output === 0) {
      const intervalId = setInterval(() => {
        makeRequest(
          'GET',
          `http://${process.env.REACT_APP_API}/is_flashdrive_mounted`
        )
          .then((res) => {
            setMountFlash(() => ({
              ...mountFlash,
              output: res?.output,
            }));
          })
          .catch((err) => {
            console.error(err);
          });
      }, timeUsb);

      return () => {
        clearInterval(intervalId);
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const downloadReportAll = async () => {
    try {
      setLoading((prev) => ({
        ...prev,
        all: true,
        save: 0,
      }));

      const res = await makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/download_to_flash`
      );
      setSaveFileAll(res);
      setLoading((prev) => ({
        ...prev,
        all: false,
        save: 1,
      }));
    } catch (err) {
      console.error(err);
    }
  };
  const downloadReportDay = async () => {
    try {
      setLoading((prev) => ({
        ...prev,
        day: true,
        save: 0,
      }));

      const formattedDate = dayjs(selectedDate.date).format('DD_MMMM_YYYY');

      const res = await makeRequest(
        'GET',
        `http://${process.env.REACT_APP_API}/download_to_flash_for_date?date=${formattedDate}`
      );
      setSaveFileDay(res);
      setLoading((prev) => ({
        ...prev,
        day: false,
        save: 2,
      }));
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <>
      <div className="report_block">
        <UsbToast
          loading={loading}
          saveFileAll={saveFileAll}
          saveFileDay={saveFileDay}
        />

        <div className="report_usb">
          <ol>
            <li
              className={
                mountFlash?.output === 0 ? 'report_text border' : 'report_text'
              }
            >
              {t('Вставьте USB носитель в')} <br /> {t('порт')}
            </li>
            <img src={usb_port} alt="usb" className="usb_img" />
            <li
              className={
                mountFlash?.output === 1 ? 'report_text border' : 'report_text'
              }
            >
              {t('Скачайте файлы на USB')} <br /> {t('носитель')}
            </li>
          </ol>

          <div className="button_block">
            <div className="button_block_one">
              <button
                disabled={!mountFlash.output ? true : false}
                onClick={downloadReportAll}
                className={
                  mountFlash?.output === 1
                    ? 'button_usb'
                    : 'button_usb disabled'
                }
              >
                {t('Скачать все')}
                <Download sx={{ fontSize: 45 }} />
              </button>

              <button
                disabled={!mountFlash?.output ? true : false}
                onClick={downloadReportDay}
                className={
                  mountFlash?.output ? 'button_usb' : 'button_usb disabled'
                }
              >
                {t('Скачать за')}
                {selectedDate?.date?.split('-').reverse().slice(0, 2).join('.')}
                <Download sx={{ fontSize: 45 }} />
              </button>
            </div>
          </div>
          {mountFlash?.output === 1 && (
            <Calendar
              selectedDate={selectedDate}
              setSelectedDate={setSelectedDate}
            />
          )}
        </div>
      </div>
    </>
  );
};
