import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const accuracyData = createAsyncThunk('type/postData', async (data) => {
  try {
    const response = await axios.post(
      `http://${process.env.REACT_APP_API}/set_precision`,
      data
    );
    return response.data;
  } catch (err) {
    console.error(err);
  }
});

export const accuracySliceReducer = createSlice({
  name: 'accuracy',
  initialState: { roughSetupPercent: 1, thinSetupPercent: 4 },
  reducers: {
    changeRoughSetupPercent: (state, action) => {
      state.roughSetupPercent = action.payload;
    },
    changeThinSetupPercent: (state, action) => {
      console.log(action);
      state.thinSetupPercent = action.payload;
    },
  },
  extraReducers: (builder) => {
    // Do something while pending if you want.
    builder.addCase(accuracyData.pending, (state) => {
      state.loading = true;
    });
    // Do something when passes.
    builder.addCase(accuracyData.fulfilled, (state, action) => {
      state.loading = false;
      //   state.products = action.payload;
    });
    // Do something if fails.
    builder.addCase(accuracyData.rejected, (state, action) => {
      state.loading = false;
      state.error = action.error.message;
    });
  },
});

export default accuracySliceReducer.reducer;
export const { changeRoughSetupPercent, changeThinSetupPercent } =
  accuracySliceReducer.actions;
