import { Route, Routes } from 'react-router-dom';
import './App.css';
import { Home } from './components/pages/Home';
import { Main } from './layout/Main';
import { SortChicken } from './components/pages/SortChicken/SortChicken';
import { SettingPage } from './components/pages/SettingPage/SettingPage';
import { OverweightPage } from './components/pages/OverweightPage/OverweightPage';
import { pushButton } from './redux/buttonSlice';
import { useDispatch } from 'react-redux';

function App() {

  const dispatch = useDispatch();

  // сброс кнопки "Продолжить" при закрытии браузера
  window.addEventListener("unload", function() {
    dispatch(pushButton('stop'));
  });
  

  return (
    <div className="App">
      <Routes>
        <Route path="/" element={<Main />}>
          <Route path="/" element={<Home />} />
          <Route path="/sort" element={<SortChicken />} />
          <Route path="/overweight" element={<OverweightPage />} />
          <Route path="/setting" element={<SettingPage />} />
        </Route>
      </Routes>
    </div>
  );
}

export default App;
